/**
 * Survey123 feature report options.
 */
export class Survey123FeatureReportOptions {

  // Feature layer or table URL. required
  public featureLayerUrl: string;

  // token, required
  public token: string;

  public url?: string;

  public username?: string;

  public surveyItemId?: string;

  public portalUrl?: string;

  public locale?: string;

  /**
   * @internal
   * @param metadata 
   */
  constructor(metadata?: Survey123FeatureReportOptions) {

    const defaultValue = {
      url: 'https://survey123.arcgis.com/api/featureReport',
      itemId: null,
      token: null,
      portalUrl: 'https://www.arcgis.com',
      f: 'json'
    };

    Object.assign(this, defaultValue, metadata || {});
  }
}
